"use client"
import { useFooterQuery } from "@/hooks/queries/useFooterQuery";
import React from "react";

const FooterContact = () => {
  const { data, isLoading, isError } = useFooterQuery();

  return (
    <section>
      <h4>KONTAKT</h4>
      {data && (
        <>
          <p>{data.phone}</p>
          <p>{data.email}</p>
          <p>{data.address}</p>
        </>
      )}
    </section>
  );
};

export default FooterContact;
